body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.module {
  margin: 0 auto;
  height: 100vh;
  max-width: 1440px;
  display: flex;
  font-family: "Roboto", sans-serif;
}
@media screen and (max-width: 1000px) {
  .module {
    flex-direction: column;
    height: auto;
  }
}

.module__left_container {
  width: 51%;
}
@media screen and (max-width: 1000px) {
  .module__left_container {
    width: 95%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1440px) {
  .module__left_container {
    width: 735px;
  }
}

.module__right_container {
  width: 49%;
}
@media screen and (max-width: 1000px) {
  .module__right_container {
    width: 95%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1440px) {
  .module__right_container {
    width: 705px;
  }
}

img {
  display: block;
  margin-top: 96px;
  margin-left: auto;
  width: 505px;
  height: 330px;
}
@media screen and (max-width: 1000px) {
  img {
    margin-top: 20px;
    width: 100%;
    height: auto;
  }
}

.module__title {
  margin-top: 136px;
  margin-bottom: 0;
  margin-left: 95px;
  font-size: 42px;
  font-weight: 700;
  line-height: 50.4px;
  color: #121212;
}
@media screen and (max-width: 1000px) {
  .module__title {
    width: 95%;
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
  }
}

.module__content {
  margin-top: 24px;
  margin-bottom: 0;
  margin-left: 95px;
  width: 380px;
  height: 96px;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: #121212;
}
@media screen and (max-width: 1000px) {
  .module__content {
    width: 95%;
    height: auto;
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
  }
}

.module__button {
  margin-top: 32px;
  margin-left: 95px;
  width: 128px;
  height: 48px;
  border-radius: 32px;
  padding: 16px 40px;
  border: none;
  background-color: #E4C1AE;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  outline: none;
}
@media screen and (max-width: 1000px) {
  .module__button {
    margin-left: calc(50% - 64px);
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.modal {
  display: none;
}

.modal.is-open {
  display: block;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__container {
  background-color: #fff;
  width: 920px;
  height: 210px;
  border-radius: 4px;
  overflow-y: auto;
  font-family: "Roboto", sans-serif;
}
@media screen and (max-width: 1000px) {
  .modal__container {
    width: 70%;
    min-height: 210px;
  }
}

.modal__title {
  margin-top: -8px;
  margin-left: 95px;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
@media screen and (max-width: 1000px) {
  .modal__title {
    margin-left: 5%;
  }
}

.modal__close {
  margin-top: 40px;
  margin-left: calc(100% - 24px - 40px);
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  padding: 0;
  border: none;
  cursor: pointer;
}
@media screen and (max-width: 751px) {
  .modal__close {
    margin-bottom: 0;
  }
}
@media screen and (max-width: 1000px) {
  .modal__close {
    margin-top: 5%;
    margin-left: calc(95% - 24px);
  }
}

.modal__content {
  margin-left: 95px;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}
.modal__content span {
  font-weight: 500;
}
@media screen and (max-width: 1000px) {
  .modal__content {
    margin-left: 5%;
  }
}

.modal__clear {
  margin-top: 0;
  margin-left: calc(100% - 144px - 40px);
  width: 144px;
  height: 34px;
  border-radius: 32px;
  padding: 10px 10px;
  border: none;
  background-color: #E4C1AE;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
}
@media screen and (max-width: 751px) {
  .modal__clear {
    margin-top: 10px;
    margin-bottom: 0;
  }
}
@media screen and (max-width: 1000px) {
  .modal__clear {
    margin-bottom: 0;
  }
}

.hidden {
  display: none;
}